@font-face {
  font-family: "M PLUS 1p";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("fonts/MPLUS1p-Regular.ttf") format("truetype");
}
@font-face {
  font-family: "M PLUS Rounded 1c";
  font-style: normal;
  font-weight: 900;
  font-display: swap;
  src: url("fonts/MPLUSRounded1c-Black.ttf") format("truetype");
}
@media (min-width: 992px) {
  :root {
    --block-gap-horizontal: 48px;
  }
}
@media (min-width: 1200px) {
  :root {
    --element-gap-horizontal: 48px;
    --element-gap-vertical: 48px;
    --block-gap-horizontal: 65px;
    --block-gap-vertical: 65px;
  }
}
html {
  box-sizing: border-box;
  cursor: default;
  text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
*,
::after,
::before {
  box-sizing: inherit;
}
body {
  font-family: "M PLUS 1p", system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 1.125rem;
  line-height: 1.625rem;
  color: #171739;
  background-color: #fff;
  min-width: 320px;
  overflow-y: scroll;
  scroll-behavior: smooth;
  font-display: swap;
  hanging-punctuation: first allow-end last;
  margin: 0;
}
h1,
h2,
h3 {
  margin-top: 0;
  margin-bottom: 0;
}
h1,
.h1 {
  font-family: "M PLUS Rounded 1c", system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 1.875rem;
  line-height: 2.5rem;
  font-weight: 900;
}
@media (min-width: 768px) {
  h1,
  .h1 {
    font-size: 2.25rem;
    line-height: 2.875rem;
  }
}
@media (min-width: 1200px) {
  h1,
  .h1 {
    font-size: 3.5rem;
    line-height: 4.125rem;
  }
}
h2,
.h2 {
  font-family: "M PLUS Rounded 1c", system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 1.875rem;
  line-height: 2.5rem;
  font-weight: 900;
}
@media (min-width: 768px) {
  h2,
  .h2 {
    font-size: 2.25rem;
    line-height: 2.875rem;
  }
}
@media (min-width: 1200px) {
  h2,
  .h2 {
    font-size: 3.5rem;
    line-height: 4.125rem;
  }
}
h3,
.h3 {
  font-family: "M PLUS Rounded 1c", system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 1.625rem;
  line-height: 2.25rem;
  font-weight: 900;
}
@media (min-width: 768px) {
  h3,
  .h3 {
    line-height: 2.875rem;
  }
}
@media (min-width: 1200px) {
  h3,
  .h3 {
    font-size: 2.25rem;
  }
}
ul,
ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
}
p {
  margin-top: 0;
  margin-bottom: 0;
}
figure {
  margin: 0 auto;
}
blockquote {
  margin: 0;
}
sup,
sub {
  line-height: 1em;
  font-size: 0.75em;
}
a,
.link {
  cursor: pointer;
  color: #0884ff;
  transition-duration: 0.3s;
  transition-timing-function: ease-out;
  transition-property: color;
}
a:hover,
.link:hover {
  color: #00376e;
}
a {
  text-decoration: none;
}
a:not([href]),
a[href^="tel"] {
  color: inherit;
  cursor: default;
}
a[href^="tel"] {
  white-space: nowrap;
}
button,
[role="button"],
[type="button"],
[type="reset"],
[type="submit"] {
  cursor: pointer;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  font-weight: inherit;
  text-transform: inherit;
  color: inherit;
  text-align: left;
  padding: 0;
  border: 0;
  background-color: transparent;
  margin: 0;
}
button[disabled],
[role="button"][disabled],
[type="button"][disabled],
[type="reset"][disabled],
[type="submit"][disabled] {
  cursor: initial;
}
input[type="text"],
input[type="email"],
input[type="password"],
input[type="tel"],
input[type="search"],
select,
textarea {
  display: block;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  color: inherit;
  padding: 0;
  border: 0;
  background-color: transparent;
  margin: 0;
  appearance: none;
}
input[type="text"]::placeholder,
input[type="email"]::placeholder,
input[type="password"]::placeholder,
input[type="tel"]::placeholder,
input[type="search"]::placeholder,
select::placeholder,
textarea::placeholder {
  opacity: 1;
}
input[type="text"]::-ms-clear,
input[type="email"]::-ms-clear,
input[type="password"]::-ms-clear,
input[type="tel"]::-ms-clear,
input[type="search"]::-ms-clear,
input[type="text"]::-ms-reveal,
input[type="email"]::-ms-reveal,
input[type="password"]::-ms-reveal,
input[type="tel"]::-ms-reveal,
input[type="search"]::-ms-reveal {
  display: none;
}
input[type="text"]::-webkit-search-decoration,
input[type="email"]::-webkit-search-decoration,
input[type="password"]::-webkit-search-decoration,
input[type="tel"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-decoration,
input[type="text"]::-webkit-search-cancel-button,
input[type="email"]::-webkit-search-cancel-button,
input[type="password"]::-webkit-search-cancel-button,
input[type="tel"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-cancel-button,
input[type="text"]::-webkit-search-results-button,
input[type="email"]::-webkit-search-results-button,
input[type="password"]::-webkit-search-results-button,
input[type="tel"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-button,
input[type="text"]::-webkit-search-results-decoration,
input[type="email"]::-webkit-search-results-decoration,
input[type="password"]::-webkit-search-results-decoration,
input[type="tel"]::-webkit-search-results-decoration,
input[type="search"]::-webkit-search-results-decoration {
  display: none;
}
input[type="number"] {
  appearance: textfield;
}
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  appearance: none;
}
select {
  cursor: pointer;
}
select:-moz-focusring {
  color: transparent;
  text-shadow: 0 0 0 #000;
}
select::-ms-expand {
  display: none;
}
textarea {
  resize: none;
}
img,
video {
  max-width: 100%;
  height: auto;
  display: block;
}
svg,
use {
  transform: translateZ(0);
  fill: currentColor;
  stroke: currentColor;
  display: block;
}
hr {
  border: 0;
  height: 1px;
  margin: 0;
  width: 100px;
  background-color: rgba(0, 0, 0, 0.18);
}
.btn {
  font-size: 1.125rem;
  line-height: 1.625rem;
  color: #fff;
  background-color: #0884ff;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  vertical-align: middle;
  padding: 16px 28px;
  border-radius: 8px;
  white-space: nowrap;
  transition-duration: 0.3s;
  transition-timing-function: ease-out;
  transition-property: background-color;
}
.btn:focus {
  outline: none;
  box-shadow: 0 0 0 2px rgba(8, 132, 255, 0.5);
}
.btn:not([disabled]):hover {
  background-color: #00376e;
}
.btn:not([disabled]):active {
  transform: translateY(1px);
}
.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
  box-shadow: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.icon {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  min-height: 1.625rem;
  vertical-align: top;
}
.page {
  display: flex;
  flex-direction: column;
  min-height: calc(var(--vh, 1vh) * 100);
  overflow-x: hidden;
  background-image: linear-gradient(180deg, #F5F5FB, #F2F2F6 40%);
}
.main {
  flex-grow: 1;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
}
.scene {
  width: 100%;
  padding-top: var(--block-gap-vertical, 48px);
  padding-right: var(--block-gap-horizontal, 16px);
  padding-bottom: var(--block-gap-vertical, 48px);
  padding-left: var(--block-gap-horizontal, 16px);
}
.scene--full {
  padding-right: 0;
  padding-left: 0;
}
.grid {
  width: 100%;
}
.grid + .grid {
  margin-top: var(--element-gap-vertical, 40px);
}
.grid__wrap {
  max-width: 1648px;
  margin-left: auto;
  margin-right: auto;
}
.grid__list {
  display: flex;
  flex-wrap: wrap;
  margin-right: calc(var(--element-gap-horizontal, 16px) * -1);
  margin-bottom: calc(var(--element-gap-vertical, 40px) * -1);
}
.grid__list + .grid__list {
  margin-top: var(--element-gap-vertical, 40px);
}
.grid__item {
  width: 100%;
  border: 0 solid transparent;
  border-right-width: var(--element-gap-horizontal, 16px);
  border-bottom-width: var(--element-gap-vertical, 40px);
}
@media (min-width: 768px) {
  .grid__list--double .grid__item {
    width: 50%;
  }
}
@media (min-width: 768px) {
  .grid__list--triple .grid__item {
    width: 50%;
  }
}
@media (min-width: 992px) {
  .grid__list--triple .grid__item {
    width: 33.33%;
  }
}
@media (min-width: 768px) {
  .grid__list--quadruple .grid__item {
    width: 50%;
  }
}
@media (min-width: 992px) {
  .grid__list--quadruple .grid__item {
    width: 33.33%;
  }
}
@media (min-width: 1200px) {
  .reviews {
    background-image: url('img/subtract.png');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background-position: 60% 360px;
  }
}
@media (min-width: 1920px) {
  .reviews {
    background-image: url('img/subtract-big.png');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background-position: 20% 500px;
  }
}
.reviews__title {
  text-align: center;
}
@media (max-width: 767px) {
  .reviews__title br {
    display: none;
  }
}
@media (min-width: 1200px) {
  .reviews__slider {
    padding-right: 40px;
    padding-left: 40px;
    margin-left: -40px;
    margin-right: -40px;
  }
}
@media (max-width: 1199px) {
  .reviews__slider {
    padding-bottom: 50px;
  }
}
.reviews__prev:hover::before,
.reviews__next:hover::before {
  transform: scale(1.3);
}
@media (min-width: 1200px) {
  .reviews__prev::before,
  .reviews__next::before {
    content: '';
    position: absolute;
    border: 1px solid rgba(121, 183, 234, 0.2);
    border-radius: 50%;
    display: flex;
    width: 30px;
    height: 30px;
    transition-duration: 0.35s;
    transition-timing-function: ease-out;
    transition-property: transform;
  }
}
@media (min-width: 1920px) {
  .reviews__prev::before,
  .reviews__next::before {
    width: 70px;
    height: 70px;
  }
}
@media (max-width: 767px) {
  .reviews__prev,
  .reviews__next {
    top: 20%;
  }
}
@media (min-width: 768px) and (max-width: 1199px) {
  .reviews__prev,
  .reviews__next {
    display: none;
  }
}
@media (min-width: 1200px) {
  .reviews__prev,
  .reviews__next {
    border: 1px solid #79B7EA;
    border-radius: 50%;
    display: flex;
    width: 30px;
    height: 30px;
  }
}
@media (min-width: 1920px) {
  .reviews__prev,
  .reviews__next {
    width: 70px;
    height: 70px;
  }
}
.reviews__prev::after,
.reviews__next::after {
  font-size: 12px;
  color: #79B7EA;
  font-weight: bold;
}
@media (min-width: 1920px) {
  .reviews__prev::after,
  .reviews__next::after {
    font-size: 24px;
  }
}
@media (max-width: 767px) {
  .reviews__prev::after,
  .reviews__next::after {
    content: '';
    width: 29px;
    height: 18px;
    background-image: url('img/arrow.png');
    background-repeat: no-repeat;
    background-size: 29px 18px;
  }
}
.reviews__prev {
  left: 0;
}
@media (min-width: 1200px) {
  .reviews__prev {
    left: 4px;
  }
}
@media (min-width: 1920px) {
  .reviews__prev {
    left: 10px;
  }
}
.reviews__prev::after {
  margin-left: -2px;
}
@media (min-width: 1920px) {
  .reviews__prev::after {
    margin-left: -4px;
  }
}
.reviews__next {
  right: 0;
}
@media (min-width: 1200px) {
  .reviews__next {
    right: 4px;
  }
}
@media (min-width: 1920px) {
  .reviews__next {
    right: 10px;
  }
}
.reviews__next::after {
  margin-right: -2px;
}
@media (max-width: 767px) {
  .reviews__next::after {
    content: '';
    width: 29px;
    height: 18px;
    transform: rotate(180deg);
    background-image: url('img/arrow.png');
    background-repeat: no-repeat;
    background-size: 29px 18px;
  }
}
@media (min-width: 1920px) {
  .reviews__next::after {
    margin-right: -4px;
  }
}
.reviews__scrollbar {
  bottom: 0 !important;
  background-color: #C3DCF4;
  height: 2px !important;
}
@media (min-width: 1200px) {
  .reviews__scrollbar {
    display: none;
  }
}
.reviews__scrollbar .swiper-scrollbar-drag {
  background-color: #7CBCF3;
}
.card-review {
  padding-top: 32px;
}
.card-review__accent {
  position: relative;
  margin-bottom: 60px;
}
.card-review__icon {
  position: absolute;
  top: 50%;
  left: 47%;
  stroke: none;
  transform: translate(-50%, -50%);
  filter: drop-shadow(2px 2px 8px #77B5E7);
}
.card-review__avatar {
  position: absolute;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 50%;
  border: 1px solid #79B7EA;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.card-review__desc {
  text-align: center;
  margin-bottom: 10px;
}
.card-review__name {
  text-align: center;
  margin-bottom: 30px;
}
.card-review__text p + p {
  margin-top: 25px;
}
.sound {
  overflow: visible;
  margin-left: auto;
  margin-right: auto;
}
.sound__circle {
  transform-origin: center center;
  animation: pulse 3s linear infinite;
}
.sound__circle--second {
  animation-delay: 1s;
}
.sound__circle--third {
  animation-delay: 2s;
}
@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 0;
  }
  30% {
    opacity: 0.4;
  }
  75% {
    opacity: 0.2;
  }
  100% {
    transform: scale(1.6);
    opacity: 0;
  }
}
